.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 800px;
    padding: 25px;
    border-bottom: 1px solid rgba(209, 215, 221, 0.5);
    cursor: pointer;
    -webkit-transition: all 0.5ms;
    -o-transition: all 0.5ms;
    transition: all 0.5ms;
}

.container:hover {
    -webkit-box-shadow: 0px 0px 10px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 10px 0px rgba(55, 71, 95, 0.1)
}

.container:nth-of-type(odd) {
    background-color: #fff;
}

.picture {
    width: 40px;
    height: 40px;
    display: block;
}

.title {
    font-size: 16px;
    font-weight: 500;
    color: #37475f;
    margin-left: 20px;
    width: 42%;
}

.document-title-wrapper {
    width: 35%;
    color: #37475f;
}

.document-title-capture {
    color: rgba(156, 166, 179, 0.8);
    margin-right: 7px;
}

.activations-label {
    font-weight: 400 !important;
    font-size: 14px !important;
}



