.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /*align-items: center;*/
    margin-right: 25px;
    margin-left: 25px;
    margin-bottom: 50px;
    width: 300px;
    height: 350px;
    /*padding: 5px;*/
    border-bottom: 1px solid rgba(209, 215, 221, 0.5);
    cursor: pointer;
    -webkit-transition: all 0.5ms;
    -o-transition: all 0.5ms;
    transition: all 0.5ms;
    background-color: #fff;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 20px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 20px 0px rgba(55, 71, 95, 0.1)
}

.container:hover {
    -webkit-box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1)
}

.picture-block {
    width: 100%;
}

.picture {
    width: 100%;
    /*height: 40px;*/
    display: block;
    border-radius: 5px 5px 0px 0px;
}

.name {
    font-size: 16px;
    padding: 10px;
    font-weight: 500;
    color: #37475f;
    border-bottom: 1px solid #f0f8ff;
}

.document-description-wrapper {
    /*width: 35%;*/
    color: #37475f;
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
}



