.buttonsContainer {
    marginLeft: -4px;
    marginBottom: 4px;
}

.textContainer {
    margin-top: 12px;
    min-height: 200px;
    width: 100%;
    outline: 0;
    /*border: 1px solid #cccccc;*/
    height: 200px;
    padding: 4px 8px;
}
.button  {
    margin-top: 3px;
    outline: 0;
    border: 1px solid #cccccc;
    margin: 0px 2px;
    padding: 2px 4px;
    cursor: pointer;
    justifyContent: center;
    alignItems: center;
    backgroundColor: #FFF;
    marginLeft: 4px;
    lineHeight: 1;
}

.textArea {
    min-height: 200px;
    width: 100%;
    outline: 0;
    border: 1px solid #cccccc;
    padding: 4px 8px;
}



