.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    padding-top: 70px;
}

.title {
    color: #8691a2;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(134, 145, 162, 0.3);
    margin-bottom: 30px;
    text-align: center;
    font-size: 25px;
    font-weight: 300;
}

.form {
    width: 400px;
}

.document-input {
    margin-bottom: 20px;
    color: rgba(0, 0, 0, 0.6) !important;
}

.name-input {
    margin-bottom: 20px;
}

.name-input input {
    color: rgba(0, 0, 0, 0.6) !important;
}

.description-input {
    margin-bottom: 20px;
    color: rgba(0, 0, 0, 0.6) !important;
}

.create-btn {
    margin: 30px auto 0 !important;
    display: block !important;
    font-weight: 400 !important;
}



